import styles from './FAQ.module.css';
import commonStyles from '../common/Common.module.css';

import Header from '../header/Header';

import '../common/simple-grid.css';

import chevronDown from '../assets/chevron.svg';
import { Accordion, AccordionItem as Item } from '@szhsin/react-accordion';

const AccordionItem = ({ header, ...rest }) => (
  <Item
    {...rest}
    header={
      <>
        {header}
        <img className={styles.chevron} src={chevronDown} alt="Chevron Down" />
      </>
    }
    className={styles.item}
    buttonProps={{
      className: ({ isEnter }) =>
        `${styles.itemBtn} ${isEnter && styles.itemBtnExpanded}`,
    }}
    contentProps={{ className: styles.itemContent }}
    panelProps={{ className: styles.itemPanel }}
  />
);

function Faq() {
  return (
    <div className="App">
      <Header />

      <div className={`${commonStyles.splash} ${styles.splash}`}>
        <div className={commonStyles.titleContainer}>
          <p className={commonStyles.title}>FAQ</p>
          <div className={`${commonStyles.arrow} ${commonStyles.bounce}`} />
        </div>
      </div>
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className={styles.accordion}>
              <Accordion transition transitionTimeout={250}>
                <AccordionItem header="What is the dress code?" initialEntered>
                  Pleasee see the dress code on the Schedule page.
                </AccordionItem>
                <AccordionItem
                  header="What time should I arrive?"
                  initialEntered
                >
                  We recommend arriving at the time listed on the Schedule page
                  to ensure the ceremonies can start in a timely manner.
                </AccordionItem>

                <AccordionItem header="Is there parking available?">
                  Yes, parking is available on premise for all events.
                </AccordionItem>

                <AccordionItem header="What should I do if I have dietary restrictions?">
                  Please let us know in advance when you RSVP. We’ll do our best
                  to accommodate.
                </AccordionItem>
                <AccordionItem header="Are there accommodations for prayer?">
                  Yes, a designated quiet space will be available for guests who
                  wish to pray during the event.
                </AccordionItem>
              </Accordion>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Faq;
